const URL = process.env.URL || "/";

export default class SimpleSlider extends Component {
  state = {
    active: 0
  };

  componentDidMount() {
    this.activeSlide = 0;
  }

  onSelectSlide(slideIdx) {
    if (slideIdx < 0 || slideIdx >= PEOPLE.length) return;

    this.activeSlide = slideIdx;

    this.slide.slickGoTo(this.activeSlide);

    this.setState(
      {
        active: this.activeSlide
      },
      () => {
        window.setTimeout(() => {
          this.slide.slickGoTo(this.activeSlide);
        }, 64);
        window.setTimeout(() => {
          this.slide.slickGoTo(this.activeSlide);
        }, 300);
        window.setTimeout(() => {
          this.slide.slickGoTo(this.activeSlide);
        }, 600);
      }
    );
  }

  onSwipe(swiped) {
    if (swiped === "left") {
      this.onSelectSlide(this.activeSlide + 1);
    } else {
      this.onSelectSlide(this.activeSlide - 1);
    }
  }

  render() {
    const settings = {
      dots: false,
      infinite: false,
      speed: 700,
      slidesToShow: 5,
      slidesToScroll: 1
    };

    const value = 30;
    const unit = "vmax";

    const activePeople = this.state.active;

    return (
      <div className="people-slider-container">
        <div className="people-slider">
          <style jsx global>
            {`

            .people-slider-container {
              width: 100vw;
              height: ${value}${unit};
              margin-bottom: 5rem;
            }

            .people-slider {
              width: ${value * 5}${unit};
              margin: 0 auto;
              position: absolute;
              top: 0;
              left: 50%;
              margin-left: ${value * -2.5}${unit};
            }

            .people-item {
              transition: opacity 0.2s, transform 0.4s;
              width: ${value}${unit};
              // padding: 5px;
            }

            .people-item img {
              width: 100%;
              transition: transform 0.2s;
              transform: scale(1);
            }

            .people-item.-active img {
              transform: scale(0.95);
            }

            .people-item::before {
              content: "";
              background-image: url("${URL}static/assets/synx-hue.svg");
              background-size: cover;
              display: block;
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              opacity: 1;
            }

            .people-arrow {
              position: absolute;
              top: calc(${value / 2}${unit} - 2rem);
              height: 4rem;
              width: 4rem;
            }

            .people-arrow.-left {
              left: 2.5rem;
            }
            .people-arrow.-right {
              right: 2.5rem;
            }

            .people-description {
              background-color: rgba(26, 26, 26);
              position: absolute;
              padding: 2rem;
              top: 90%;
              right: 10rem;
              width: 55rem;
              max-width: 80vw;
              z-index: 100;
            }

            .people-description-name {
              font-size: 24px;
              padding: 12px 0;
              color: #fff;
              font-weight: 700;
            }

            .people-description-bio {
              color: #D9D8D6;
              font-size: 18px;
            }

            .people-description-title {
              opacity: 0.6;
              font-weight: 400;
            }

            @media screen and (min-width: 1200px) {
              .people-slider {
                margin-left: ${value * -3}${unit};
              }
            }

            @media screen and (max-width: 1199px) {
              .people-slider-container {
                height: auto;
              }

              .people-description-title {
                display: block;
              }

              .people-description {
                width: 100vw;
                position: relative;
                top: 0;
                right: 0;
                top: ${value}${unit};
                margin: 0 auto ${value}${unit};
              }
            }


          `}
          </style>

          <Slider
            {...settings}
            className="react-slider-component"
            ref={el => (this.slide = el)}
            arrows={false}
            centerMode={true}
            touchThreshold={10}
            onSwipe={this.onSwipe.bind(this)}
            swipeToSlide={true}>
            {PEOPLE.map((person, personIdx) => {
              const intensity = Math.min(10 - Math.abs(activePeople - personIdx)) / 10;
              const polarity = activePeople < personIdx ? 1 : -1;

              const translateValue = Math.round((polarity * (intensity - 1) * 100) / Math.pow(intensity, 3));

              const style = {
                opacity: Math.pow(intensity, 10),
                transform: `scale(${intensity}) translatex(${translateValue}%)`,
                zIndex: Math.abs(intensity * 100)
              };

              return (
                <div onClick={this.onSelectSlide.bind(this, personIdx)} key={personIdx}>
                  <div className={`people-item ${personIdx === activePeople ? "-active" : ""}`} style={style}>
                    <img src={`./static/assets/people/${person.key}.jpg`} />
                  </div>
                </div>
              );
            })}

            <div className="people-item" />
            <div className="people-item" />
            <div className="people-item" />
            <div className="people-item" />
          </Slider>
        </div>

        <div className="people-description">
          <h3 className="people-description-name">
            {PEOPLE[activePeople].name} <span className="people-description-title"> • {PEOPLE[activePeople].position}</span>
          </h3>

          <div className="people-description-bio">
            {PEOPLE[activePeople].description}
          </div>
        </div>

        <img className="people-arrow -left" src={`./static/assets/arrow-left.svg`} onClick={this.onSwipe.bind(this, "right")} />
        <img className="people-arrow -right" src={`./static/assets/arrow-right.svg`} onClick={this.onSwipe.bind(this, "left")} />
      </div>
    );
  }
}
